/* eslint-disable react/jsx-one-expression-per-line */
import React, { Component } from "react";
import photo from "./me_october-2018.png";

class About extends Component {
  render() {
    return (
      <div className="about">
        <div className="hero">
          <h1>
            About me
          </h1>
          <p>
            My name’s Edwin and I’m a student.
          </p>
        </div>

        <div className="columns one">
          <div className="panel">
            <h2>
              My Skillset
            </h2>
            <ul className="left-list">
              <li>wait what am I good at?</li>
            </ul>
            <ul className="right-list">
              <li>HTML5</li>
              <li>CSS3</li>
              <li>JavaScript (ES6, React)</li>
              <li>Version Control (GitHub)</li>
            </ul>
          </div>
        </div>
        <div className="columns two">
          <div className="panel">
            <h2>
              How I got started
            </h2>
            <div className="bio">
              <p>
                After studying Computer Science and realizing how useless most of the things i was learning were. I decided to make this website.
              </p>
              <p>
                I began to play around with HTML, then reusing html templates then bootstrap templates then gatsby.
              </p>
              <p>
                After that, I hope i have enough credentials to land a position as a jr dev or an internship.
              </p>
            </div>
          </div>
          <img className="photo" src={photo} alt="Me in October of 2018" />
        </div>
      </div>
    );
  }
}

export default About;
